import React, { Component } from 'react';
import { Button, OverlayTrigger, Popover } from 'react-bootstrap';
import Axis from './Axis';
import Country from './Country';

import { containerHeight } from '../config/config';
import data from '../data/countryData';

const initiallyShowingCountries = {};
data.forEach(datum => initiallyShowingCountries[datum.name] = true);

class DataVizContainer extends Component {
	constructor(props) {
		super(props);
		this.state={
			showing: {
				fish: true,
				broilers: true,
				hens: true,
				pigs: true,
				other: true,
			},
			countries: initiallyShowingCountries,
		}
	}
	
	handleCountryToggle = name => {
		this.setState({
			countries: Object.assign(
				{},
				this.state.countries,
				{ [name]: !this.state.countries[name] }	
			)
		});
	}
	
	handleFishToggle = () => {
		this.setState({
			showing: Object.assign(
				{},
				this.state.showing,
				{
					fish: !this.state.showing.fish,
				}
			),
		});
	}
	
	handleBroilersToggle = () => {
		this.setState({
			showing: Object.assign(
				{},
				this.state.showing,
				{
					broilers: !this.state.showing.broilers,
				}
			),
		});
	}
	
	handleHensToggle = () => {
		this.setState({
			showing: Object.assign(
				{},
				this.state.showing,
				{
					hens: !this.state.showing.hens,
				}
			),
		});
	}
	
	handlePigsToggle = () => {
		this.setState({
			showing: Object.assign(
				{},
				this.state.showing,
				{
					pigs: !this.state.showing.pigs,
				}
			),
		});
	}
	
	handleOtherToggle = () => {
		this.setState({
			showing: Object.assign(
				{},
				this.state.showing,
				{
					other: !this.state.showing.other,
				}
			),
		});
	}
	
	selectAllCountries = () => {
		const newCountries = Object.assign({}, this.state.countries);
		data.forEach(datum => newCountries[datum.name] = true);
		this.setState({
			countries: newCountries,
		});
	}
	
	unselectAllCountries = () => {
		const newCountries = Object.assign({}, this.state.countries);
		data.forEach(datum => newCountries[datum.name] = false);
		this.setState({
			countries: newCountries,
		});
	}
	
  render() {
		const popoverBottom = (
		  <Popover id="popover-positioned-bottom" title="Filter by Country">
		    {
					data.map(datum => <div>
						<input
							checked={this.state.countries[datum.name]}
							type="checkbox"
							onChange={() => this.handleCountryToggle(datum.name)}
						/>
						<span style={{marginLeft: '5px'}}>{datum.name}</span>
						</div>)
				}
		  </Popover>
		);
		
		const maxValue = Math.max(...data.map(datum => {
			if (!this.state.countries[datum.name]) return 0;
			return (
				(this.state.showing.fish ? datum.fish : 0)
				+
				(this.state.showing.broilers ? datum.broilers : 0)
				+
				(this.state.showing.hens ? datum.hens : 0)
				+
				(this.state.showing.pigs ? datum.pigs : 0)
				+
				(this.state.showing.other ? datum.total - (datum.fish + datum.broilers + datum.hens + datum.pigs) : 0)
			);
		}));
		
		return (
			<div 
				style={{
					backgroundColor: '#f8f8f8',
					border: '1px solid #ddd',
					height: '700px',
					margin: '45px auto 0',
					padding: '30px 300px 0 30px',
					position: 'relative',
					width: `${980 + 280}px`,
				}}
			>
				<div
					style={{
						backgroundColor: '#fff',
						border: '1px solid #ddd',
						width: '980px',
					}}
				>	
					<div
						style={{
							position: 'absolute',
							top: '30px',
							right: '40px',
						}}
					>
						<div style={{ marginBottom: '6px', textAlign: 'left' }}>
							<strong>Filter by Animal</strong>
						</div>
						<div
							style={{
								alignItems: 'center',
								display: 'flex',
								marginBottom: '10px',
							}}
						>
							<input
								checked={this.state.showing.fish}
								onChange={this.handleFishToggle}
								type="checkbox" 
							/>
							<span
								style={{
									borderRadius: '10px',
									boxShadow: '1px 1px 3px #999', 
									backgroundColor: 'lightBlue',
									display: 'inline-block',
									height: '20px',
									margin: '0 5px',
									padding: '10px',
									width: '20px',
								}}
							></span>
							<span
								style={{
									marginBottom: 0, // overide Bootstrap 5px bottom margin
								}}
							>Farmed Fish</span>
						</div>
						<div
							style={{
								alignItems: 'center',
								display: 'flex',
								marginBottom: '10px',
							}}
						>
							<input 
								checked={this.state.showing.broilers}
								onChange={this.handleBroilersToggle}
								type="checkbox" 
							/>
							<span
								style={{
									borderRadius: '10px', 
									boxShadow: '1px 1px 3px #999',
									backgroundColor: 'tan',
									display: 'inline-block',
									height: '20px',
									margin: '0 5px',
									padding: '10px',
									width: '20px',
								}}
							></span>
							<span
								style={{
									marginBottom: 0, // overide Bootstrap 5px bottom margin
								}}
							>Chickens for Meat</span>
						</div>
						<div
							style={{
								alignItems: 'center',
								display: 'flex',
								marginBottom: '10px',
							}}
						>
							<input 
								checked={this.state.showing.hens}
								onChange={this.handleHensToggle}
								type="checkbox" 
							/>
							<span
								style={{
									borderRadius: '10px', 
									boxShadow: '1px 1px 3px #999',
									backgroundColor: 'beige',
									display: 'inline-block',
									height: '20px',
									margin: '0 5px',
									padding: '10px',
									width: '20px',
								}}
							></span>
							<span
								style={{
									marginBottom: 0, // overide Bootstrap 5px bottom margin
								}}
							>Hens for Eggs</span>
						</div>
						<div
							style={{
								alignItems: 'center',
								display: 'flex',
								marginBottom: '10px',
							}}
						>
							<input 
								checked={this.state.showing.pigs}
								onChange={this.handlePigsToggle}
								type="checkbox" 
							/>
							<span
								style={{
									borderRadius: '10px', 
									boxShadow: '1px 1px 3px #999',
									backgroundColor: 'pink',
									display: 'inline-block',
									height: '20px',
									margin: '0 5px',
									padding: '10px',
									width: '20px',
								}}
							></span>
							<span
								style={{
									marginBottom: 0, // overide Bootstrap 5px bottom margin
								}}
							>Pigs</span>
						</div>
						<div
							style={{
								alignItems: 'center',
								display: 'flex',
								marginBottom: '10px',
							}}
						>
							<input
								checked={this.state.showing.other}
								onChange={this.handleOtherToggle}
								type="checkbox" 
							/>
							<span
								style={{
									borderRadius: '10px',
									boxShadow: '1px 1px 3px #999', 
									backgroundColor: 'lightGray',
									display: 'inline-block',
									height: '20px',
									margin: '0 5px',
									padding: '10px',
									width: '20px',
								}}
							></span>
							<span
								style={{
									marginBottom: 0, // overide Bootstrap 5px bottom margin
								}}
							>Other Farmed Animals</span>
						</div>
						
						<div style={{ marginBottom: '25px' }} />
						
						<div style={{ textAlign: 'left' }}>
							<div style={{ marginBottom: '10px'}}> <strong>Filter by Region</strong> </div>
							<Button bsSize="xsmall" style={{marginRight: '8px'}} onClick={this.selectAllCountries}>select all</Button><Button bsSize="xsmall" onClick={this.unselectAllCountries}>unselect all</Button>
							<div style={{marginBottom: '10px'}} />
							{
								data.map(datum => <div style={{ marginBottom: '6px'}}>
									<input
										checked={this.state.countries[datum.name]}
										type="checkbox"
										onChange={() => this.handleCountryToggle(datum.name)}
									/>
									<span style={{marginLeft: '5px'}}>{datum.name}</span>
									</div>)
							}
						</div>
						
					</div>
					<div
						style={{
							color: '#777',
							paddingTop: '10px',
							textAlign: 'center',
							width: '980px',
						}}
					>click on a bar for more detailed information</div>
					<svg
						height={containerHeight}
						width="980"
					>
						<Axis maxValue={maxValue}/>
						<g
							transform="translate(80, 120)"
						>
							{ data.filter(datum => this.state.countries[datum.name]).map((datum, i) => 
									<Country
										datum={datum}
										key={datum.name}
										i={i}
										maxValue={maxValue}
										showing={this.state.showing}
									/>
								)
							}
						</g>
					</svg>
				</div>
			</div>
    );
  }
}

export default DataVizContainer;


