import React, { Component } from 'react';
import { OverlayTrigger, Popover } from 'react-bootstrap';

import BroilersRect from './BroilersRect';
import FishRect from './FishRect';
import HensRect from './HensRect';
import PigsRect from './PigsRect';
import OtherRect from './OtherRect';

import { barHeight } from '../config/config';

class Country extends Component {
  render() {
		const popoverTop = (
		  <Popover id={`popover-positioned-top-${this.props.i}`} title={<div style={{ borderBottom: '1px solid lightGray', fontSize: '18px', paddingBottom: '4px', textAlign: 'center' }}>{this.props.datum.name}</div>}>
		    <div style={{display: 'grid', gridTemplateColumns: 'repeat(2, 150px)', gridGap: '10px' }}>
					<div style={{ gridColum: '1 / 3', gridRow: '1' }}>
						<div style={{ alignItems: 'center', display: 'flex'}}>
							<span
								style={{
									borderRadius: '10px',
									boxShadow: '1px 1px 3px #999', 
									backgroundColor: 'lightBlue',
									display: 'inline-block',
									height: '20px',
									marginRight: '5px',
									width: '20px',
								}}
							></span>
							<strong>Farmed Fish</strong>
						</div>
						<em>{(this.props.datum.fish * 1000000).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</em>
					</div>
					<div style={{ }}>
						<div style={{ alignItems: 'center', display: 'flex'}}>
							<span
								style={{
									borderRadius: '10px',
									boxShadow: '1px 1px 3px #999', 
									backgroundColor: 'tan',
									display: 'inline-block',
									height: '20px',
									marginRight: '5px',
									width: '20px',
								}}
							></span>
							<strong>Meat Chickens</strong>
						</div>
						<em>{(this.props.datum.broilers * 1000000).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</em>
					</div>
					<div>
						<div style={{ alignItems: 'center', display: 'flex'}}>
							<span
								style={{
									borderRadius: '10px',
									boxShadow: '1px 1px 3px #999', 
									backgroundColor: 'beige',
									display: 'inline-block',
									height: '20px',
									marginRight: '5px',
									width: '20px',
								}}
							></span>
							<strong>Hens for Eggs</strong>
						</div>
						<em>{(this.props.datum.hens * 1000000).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</em>
					</div>
					<div>
						<div style={{ alignItems: 'center', display: 'flex'}}>
							<span
								style={{
									borderRadius: '10px',
									boxShadow: '1px 1px 3px #999', 
									backgroundColor: 'pink',
									display: 'inline-block',
									height: '20px',
									marginRight: '5px',
									width: '20px',
								}}
							></span>
							<strong>Pigs</strong>
						</div>
						<em>{(this.props.datum.pigs * 1000000).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</em>
					</div>
					<div style={{ marginBottom: '4px', paddingBottom: '4px' }}>
						<div style={{ alignItems: 'center', display: 'flex'}}>
							<span
								style={{
									borderRadius: '10px',
									boxShadow: '1px 1px 3px #999', 
									backgroundColor: 'lightGray',
									display: 'inline-block',
									height: '20px',
									marginRight: '5px',
									width: '20px',
								}}
							></span>
							<strong>Other</strong>
						</div>
						<em>{((this.props.datum.total - this.props.datum.fish - this.props.datum.broilers - this.props.datum.hens - this.props.datum.pigs) * 1000000).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</em>
					</div>
				</div>
				
			</Popover>
		);
		
    return (
			
				<g transform={`translate(${this.props.i * 60}, 0)`} >
					<OverlayTrigger trigger="click" placement="top" overlay={popoverTop} rootClose={true}>
						<g style={{ cursor: 'pointer' }}>
							<FishRect
								datum={this.props.datum}
								maxValue={this.props.maxValue}
								showing={this.props.showing}
							/>
							<BroilersRect
								datum={this.props.datum}
								maxValue={this.props.maxValue}
								showing={this.props.showing}
							/>
							<HensRect
								datum={this.props.datum}
								maxValue={this.props.maxValue}
								showing={this.props.showing}
							/>
							<PigsRect
								datum={this.props.datum}
								maxValue={this.props.maxValue}
								showing={this.props.showing}
							/>
							<OtherRect
								datum={this.props.datum}
								maxValue={this.props.maxValue}
								showing={this.props.showing}
							/>
						</g>
	    		</OverlayTrigger>
					<text
						style={{
							fill: '#222',
							fontFamily: 'sans-serif',
							fontWeight: '600',
						}}
						textAnchor="end"
						transform={`rotate(-60, 0, ${barHeight + 50})`}
						x={40}
						y={barHeight + 50}
					>{this.props.datum.name}</text>
				</g>
    );
  }
}

export default Country;


